import React, { Component, Fragment } from 'react'
import { getDaysInMonth, monthEn, monthThShort } from '../utils/date'
import { Button } from 'antd'
import { connect } from 'react-redux'

const years = [2018, 2019, 2020]
const width = 55
const ButtonGroup = Button.Group

class DatePickerExpand extends Component {
  renderRow = (data, attr, useIndex) => {
    const { name, value } = this.props
    return <div className="col-md-12 pl-0 mb-2">
      <ButtonGroup>
        {data.map((val, i) => (
          <Button
            style={{ width }}
            onClick={() => {
              value[attr] = useIndex ? i : val
              this.props.onChange({ name: name, value: JSON.parse(JSON.stringify(value)) })
            }}
            key={i}
            type={value[attr] === (useIndex ? i : val) ? "primary" : ''}>{val}</Button>
        ))}
      </ButtonGroup>
    </div>
  }

  renderRowDate = (data) => {
    const { name, value } = this.props
    return <div className="col-md-12 pl-0 mb-2">
      <ButtonGroup>
        {data.map((d, i) => (
          <Button
            style={{ width }}
            onClick={() => {
              const date = d.getDate()
              const index = value.days.findIndex(x => x === date)
              if (index === -1) {
                value.days.push(date)
              } else {
                value.days.splice(index, 1)
              }
              this.props.onChange({ name: name, value: JSON.parse(JSON.stringify(value)) })
            }}
            key={i}
            type={value.days.indexOf(d.getDate()) > -1 ? "primary" : ''}>{d.getDate()}</Button>
        ))}
      </ButtonGroup>
    </div>
  }

  render() {
    const { value, language } = this.props
    const month = language === 'th' ? monthThShort : monthEn.map(x => x.substring(0, 3))
    const days = getDaysInMonth(value.month, value.year)
    return (
      <Fragment>
        {this.renderRow(years, 'year')}
        {this.renderRow(month, 'month', true)}

        {this.renderRowDate(days.slice(0, 15))}
        {this.renderRowDate(days.slice(15))}
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(DatePickerExpand)
