import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import Render from '../../components/Renderer'
import thImg from '../../images/th.png'
import { Upload, Button, Icon, Switch, Divider } from 'antd'
import { ChromePicker } from 'react-color'

const props = {
  action: '//jsonplaceholder.typicode.com/posts/',
  listType: 'picture',
}

class AirlineEdit extends Component {
  state = {
    form: {},
    fileList: []
  }

  componentDidMount() {
    const { match } = this.props
    if (match && match.params && match.params.id) {
      // loca form and set state
    }
    this.setState({
      form: {
        key: 1,
        no: 1,
        airline: 'NOK AIR',
        icao: 'NOK',
        iata: 'DD',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        country: 'THAILAND',
        logo: thImg,
        callsign: '',
        color: '#ee4a54',
        display: true,
        updateDate: '1 August 2018 08:33',
      }
    })
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  handleUpload = ({ fileList }) => this.setState({ fileList })

  render() {
    const { form, fileList } = this.state
    const template = [{
      type: 'input',
      name: 'airline',
      label: 'Airline',
      value: '',
      placeholder: '',
    }, {
      type: 'input',
      name: 'icao',
      label: 'ICAO',
      value: '',
      placeholder: '',
    }, {
      type: 'input',
      name: 'iata',
      label: 'IATA',
      value: '',
      placeholder: '',
    }, {
      type: 'input',
      name: 'companyName',
      label: 'Company Name',
      value: '',
      placeholder: '',
    }, {
      type: 'customselect',
      name: 'country',
      label: 'Country',
      value: '',
      placeholder: 'Select...',
      options: []
    }]
    return (
      <div className="card card-body card-body-component">
        <div className="row mb-2">
          <div className="col-auto">
            <Link to="/airlinesetting">
              <FontAwesomeIcon icon="angle-double-left" /><span className="ml-2">Back</span>
            </Link>
          </div>
        </div>

        {template.map((data, i) => {
          return <div key={i} className="form-row form-inline mb-2 col-12">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">{data.label} :</label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(data, this.onChange)}
            </div>
          </div>
        })}

        <div className="form-row form-inline mb-2 col-12 d-flex align-items-start">
          <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">Logo :</label>
          <div className="col-lg-4 col-md-6 col-sm-8 col-12">
            <Upload {...props} onChange={this.handleUpload} disabled={fileList.length === 1} fileList={fileList}>
              <Button>
                <Icon type="upload" /> Upload
              </Button>
              <div className='text-warning'>
                Only JPG, PNG and JPEG file are allowed
              </div>
              <div className='text-warning'>
                Maximum upload file size 500 KB, and 250x85 pixel
              </div>
            </Upload>
          </div>
        </div>

        <div className="form-row form-inline mb-2 col-12">
          <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">Color :</label>
          <div className="col-lg-4 col-md-6 col-sm-8 col-12">
            <ChromePicker />
          </div>
        </div>

        <div className="form-row form-inline mb-2 col-12">
          <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">Display (On/Off) :</label>
          <div className="col-lg-4 col-md-6 col-sm-8 col-12">
            <Switch checked={form.display} onChange={checked => {
              let { form } = this.state
              form.display = checked
              this.setState({ form })
            }} />
          </div>
        </div>

        <Divider />

        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={this.props.onClose}
            className="btn btn-light mr-2">
            Cancel
          </button>
          <button
            onClick={this.props.onClose}
            className="btn btn-success mr-2">
            Save
          </button>
        </div>
      </div>
    );
  }
}

export default AirlineEdit