import React, { Component } from 'react'
import { Select } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import AdvanceSearch from './AdvanceSearch'
import { getFlightStat } from '../../redux/actions/flight'
import { connect } from 'react-redux'
import context from '../../utils/language'
import Render from '../../components/Renderer'
import { normalSearch as nConstraints } from './Constraints'

const children = []
for (let i = 10; i < 36; i++) {
  children.push(<Select.Option key={i.toString(36) + i}>{i.toString(36) + i}</Select.Option>)
}

class SearchComponent extends Component {
  state = {
    show: false,
    icon: <FontAwesomeIcon icon="angle-double-down" />,
    form: {}
  }

  componentDidMount() {
    this.props.getFlightStat()
  }

  toggleAdvSearch = () => {
    this.setState({
      show: !this.state.show,
      icon: !this.state.show ? (
        <FontAwesomeIcon icon="angle-double-up" />
      ) : (
          <FontAwesomeIcon icon="angle-double-down" />
        )
    })
  }

  search = () => {
    const { form, show } = this.state
    if (!show) { // normal search
      let param = ''
      if (form.flightDate && form.flightDate.length > 0) param = 'flightDate=' + form.flightDate
      if (form.airportID) param += '&airportID=' + form.airportID
      if (form.airlineID) param += '&airlineID=' + form.airlineID
      if (form.flightNo) param += '&flightNo=' + form.flightNo
      this.props.getFlightStat(param)
    } else { // advance search
      console.log('advance search', form)
    }
  }

  reset = () => this.setState({ form: {} })

  today = () => {
    console.log('today')
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  render() {
    const { dropdown, language } = this.props
    let { form, text, icon, show } = this.state
    const name = context[language].search
    const options = dropdown.airPortOptions.map(x => {
      x.id = x.airportID
      x.text = x.airportName
      return x
    })
    const optionsAirline = dropdown.airlineOptions.map(x => {
      x.id = x.airlineID
      x.text = x.showFilter
      return x
    })
    text = !show ? (name && name.more) : (name && name.less)

    const template = [
      {
        type: 'customselect',
        name: 'airportID',
        label: name && name.airport,
        value: form.airportID,
        placeholder: name && name.airportPlaceholder,
        options: options
      }, {
        type: 'datepicker',
        name: 'flightDate',
        label: name && name.date,
        value: form.flightDate,
        constraint: nConstraints,
        placeholder: name && name.datePlaceholder,
      }, {
        type: 'customselect',
        name: 'airlineID',
        label: name && name.airline,
        value: form.airlineID,
        placeholder: name && name.airlinePlaceholder,
        options: optionsAirline
      }, {
        type: 'customselect',
        name: 'flightNo',
        label: name && name.flightNo,
        value: form.flightNo,
        placeholder: name && name.flightNoPlaceholder,
        options: []
      }
    ]
    return (
      <div className="card card-body bg-light border-0 mb-3">
        <div className="form-row justify-content-between">
          {template.map((t, i) => (
            <div key={i} className="col-12 col-md-6 col-xl form-inline form-row mb-2">
              <label className="col-6 col-xl-5 justify-content-end">{t.label} :</label>
              <div className="col col-xl-7">
                {Render(t, this.onChange)}
              </div>
            </div>
          ))}
        </div>

        {/*  */}
        <AdvanceSearch
          show={show}
          dropdown={dropdown}
          form={form}
          onChange={(key, value) => {
            form[key] = value
            this.setState({ form })
          }}
        />

        <div className="row mt-4">
          <div className="col text-center">
            <button onClick={this.reset} className="btn btn-dark mr-2">
              <FontAwesomeIcon icon="redo" />{name && name.clear}
            </button>
            <button onClick={this.search} className="btn btn-success">{name && name.search}</button>
          </div>
          <div className="col-auto">
            <button
              type="button"
              className="btn btn-secondary"
              style={{ width: '100px' }}
              onClick={this.toggleAdvSearch}>
              {text} {icon}
            </button>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

const mapDispatchToProps = dispatch => ({
  getFlightStat: (param) => dispatch(getFlightStat(param))
})

export default connect(mapStateToProps, mapDispatchToProps)(SearchComponent)
