import React, { Component } from 'react'
import { Table, Switch } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import thImg from '../../images/th.png'

class AirlineSetting extends Component {
  state = {
    form: [
      {
        key: 1,
        no: 1,
        airline: 'NOK AIR',
        icao: 'NOK',
        iata: 'DD',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        country: 'THAILAND',
        logo: thImg,
        callsign: '',
        color: '#ee4a54',
        display: true,
        updateDate: '1 August 2018 08:33',
      }, {
        key: 2,
        no: 2,
        airline: 'NOK AIR',
        icao: 'NOK',
        iata: 'DD',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        country: 'THAILAND',
        logo: thImg,
        callsign: '',
        color: '#ee4a54',
        display: true,
        updateDate: '1 August 2018 08:33',
      }, {
        key: 3,
        no: 3,
        airline: 'NOK AIR',
        icao: 'NOK',
        iata: 'DD',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        country: 'THAILAND',
        logo: thImg,
        callsign: '',
        color: '#ee4a54',
        display: true,
        updateDate: '1 August 2018 08:33',
      }
    ]
  }

  edit = (item) => {
    const path = item ? ('/' + item.key) : ''
    this.props.history.push('/airlinesetting/edit' + path)
  }
  add = () => this.props.history.push('/airlinesetting/add')

  render() {
    const { form } = this.state

    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
      }, {
        title: 'Airline',
        dataIndex: 'airline',
        key: 'airline'
      }, {
        title: 'ICAO',
        dataIndex: 'icao',
        key: 'icao'
      }, {
        title: 'IATA',
        dataIndex: 'iata',
        key: 'iata'
      }, {
        title: 'Company Name',
        dataIndex: 'companyName',
        key: 'companyName'
      }, {
        title: 'Country',
        dataIndex: 'country',
        key: 'country'
      }, {
        title: 'Logo',
        dataIndex: 'logo',
        key: 'logo',
        render: image => <img height="35" width="80" src={image} alt='logo' />
      }, {
        title: 'Callsign',
        key: 'callsign',
        render: () => (
          <button onClick={this.view} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Color',
        dataIndex: 'color',
        key: 'color',
        render: color => <div style={{ backgroundColor: color, width: 20, height: 20, borderRadius: 3 }} />
      }, {
        title: 'Display(On/Off)',
        dataIndex: 'display',
        key: 'display',
        render: display => <Switch checked={display} />
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate'
      }, {
        title: 'Edit',
        key: 'edit',
        render: item => (
          <button onClick={() => this.edit(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        render: () => (
          <button onClick={this.delete} type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button>
        )
      }
    ]

    const table = <Table
      style={{ minWidth: 1200 }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={form} />
    return (
      <div className="card card-body card-body-component">
        <div className="card card-body card-header-table">
          <div className="mb-3">
            <button type="button" onClick={this.add} className="btn btn-success">
              <FontAwesomeIcon icon="plus" /> Add Airline
          </button>
          </div>
          {table}
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(AirlineSetting)
