import React, { Component, Fragment } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import Render from '../../components/Renderer'
import Background from '../../images/img-login.png'
import { connect } from 'react-redux'
// import context from '../../utils/language'
import { dayEn } from '../../utils/date'
import DatePickerExpand from '../../components/DatePickerExpand'
import FlightEditTable from './FlightEditTable'

class FlightEdit extends Component {
  state = {
    form: {
      dateType: 'date',
      dateValue: {
        year: 2018,
        month: 8,
        days: [10, 12, 13]
      },
      from: 'Don Mueang',
      to: 'Suratthani',
      data: [{
        key: 1,
        flightNoPre: 'DD',
        flightNo: '9704',
        depTime: '08:00',
        arrTime: '09:10',
        lcd: true,
        flightNo2Pre: 'DD',
        flightNo2: '9705',
        depTime2: '09:40',
        arrTime2: '10:30',
        lcd2: true
      }, {
        key: 2,
        flightNoPre: 'DD',
        flightNo: '8810',
        depTime: '08:00',
        arrTime: '09:10',
        lcd: true,
        flightNo2Pre: 'DD',
        flightNo2: '8811',
        arrTime2: '10:30',
        lcd2: false
      }, {
        key: 3,
        flightNo: '',
        depTime: '',
        arrTime: '',
        lcd: false,
        flightNo2: '',
        depTime2: '',
        arrTime2: '',
        lcd2: false
      }]
    }
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  render() {
    const { form } = this.state
    const { dropdown } = this.props
    const airportList = {
      type: 'select',
      name: 'flightStatsID',
      label: 'Airport',
      value: '',
      placeholder: '',
      options: []
    }

    const template = [{
      type: 'select',
      name: 'airport',
      label: 'Flight Code',
      value: form.airport,
      placeholder: '',
      options: dropdown.agOptions
    }, {
      type: 'radiogroup',
      name: 'dateType',
      label: 'Date',
      value: form.dateType,
      placeholder: '',
      options: [
        { text: 'Date range', value: 'range' },
        { text: 'Date', value: 'date' }
      ]
    }, {
      type: 'rangepicker',
      name: 'dateRange',
      language: 'th',
      value: form.dateRange,
    }, {
      type: 'checkall',
      label: 'All',
      name: 'dateList',
      value: form.dateList || [],
      options: dayEn.map((x, i) => ({ label: x, value: i }))
    }, {
      type: 'customselect',
      label: 'Airport',
      name: 'airportFrom',
      placeholder: 'Select...',
      value: form.airportFrom,
      options: dropdown.airPortOptions.map(x => ({
        id: x.airportID,
        text: x.airportName
      }))
    }, {
      type: 'customselect',
      name: 'airportTo',
      placeholder: 'Select...',
      value: form.airportTo,
      options: dropdown.airPortOptions.map(x => ({
        id: x.airportID,
        text: x.airportName
      }))
    }]

    return (
      <div className="card card-body card-body-component">
        <div className="row mb-2 form-inline">
          <img src={Background} alt={'lbg'} style={{
            position: 'absolute',
            top: 60,
            right: 20,
            opacity: 0.7,
            height: '35%',
          }} />
          <div className="col-md-3">
            <Link to="/flightschedule">
              <FontAwesomeIcon icon="angle-double-left" /><span className="ml-2">Back</span>
            </Link>
          </div>
          <div className="col-md-9 form-inline justify-content-end mb-3">
            <label className={"justify-content-end pr-0 col-md-4 col-sm-5 col-3"}>{airportList.label} :</label>
            <div className={"col-md-4 col-sm-5 col-8 pr-0"}>
              {Render(airportList, () => { })}
            </div>
          </div>

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-5 justify-content-end">{template[0].label} :</label>
            <div className="col-md-1 col-7">
              {Render(template[0], this.onChange)}
            </div>
            <label className="col-md-3 col-7 text-primary justify-content-start">Internation Flight</label>
          </div>

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-3 justify-content-end">{template[1].label} :</label>
            <div className="col-md-9 col-9">
              {Render(template[1], this.onChange)}
            </div>
          </div>

          {form.dateType === 'range' && <Fragment>
            <div className="col-12 col-lg-12 form-inline form-row mb-3">
              <div className="col-md-3 col-3" />
              <div className="col-md-4 col-7">
                {Render(template[2], this.onChange)}
              </div>
            </div>
            {/*<div className="col-12 col-lg-12 form-inline form-row mb-3">
              <div className="col-md-3 col-3" />
              <div className="col-md-9 col-9">
                {Render(template[3], this.onChange)}
              </div>
            </div>*/}
          </Fragment>}

          {form.dateType === 'date' && <div style={{ minWidth: 1200 }} className="col-12 col-lg-12 form-inline form-row mb-3">
            <div className="col-md-3 col-3" />
            <div className="col-md-9 col-12 ml-0">
              <DatePickerExpand name="dateValue" value={form.dateValue} onChange={this.onChange} />
            </div>

          </div>}

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-3 justify-content-end">{template[4].label} :</label>
            <div className="col-md-3 col-sm-4 col-12">
              {Render(template[4], this.onChange)}
            </div>
            <FontAwesomeIcon icon="angle-double-right" />
            <div className="col-md-3 col-sm-4 col-12">
              {Render(template[5], this.onChange)}
            </div>
          </div>

        </div>

        <div className="card card-body card-header-table">
          <FlightEditTable
            onChange={newForm => {
              form.data = JSON.parse(JSON.stringify(newForm))
              this.setState(form)
            }}
            form={form.data}
            from={form.from}
            to={form.to} />
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

export default connect(mapStateToProps)(FlightEdit)
