import React from 'react'
import {
  Input,
  Popover,
  DatePicker,
  Select,
  Radio,
  Checkbox,
  TimePicker
} from 'antd'
import moment from 'moment'
import TextArea from 'antd/lib/input/TextArea'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import localeTH from 'antd/lib/date-picker/locale/th_TH'
import localeEN from 'antd/lib/date-picker/locale/en_US'
import CustomDropdown from './Dropdown'
import validatejs from 'validate.js'

const { RangePicker } = DatePicker

const validate = data => {
  let valid = undefined
  if (data.constraint) {
    valid = validatejs({ [data.name]: data.value }, data.constraint)
  }
  return valid
}

const renderDatepicker = (data, onChange) => {
  const valid = validate(data)
  return <DatePicker
    style={{ width: '100%' }}
    name={data.name}
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    onChange={(date, dateString) => onChange({ name: data.name, value: dateString, error: valid })}
    value={data.value ? moment(data.value) : null} // with format use moment(data.value, 'YYYYMMDD')
  />
}

const renderInput = (data, onChange) => {
  const valid = validate(data)
  return <Input
    disabled={data.disabled}
    addonAfter={
      data.addon && <Popover content={data.addon.detail} title={data.addon.title}>
        <FontAwesomeIcon icon={data.addon.icon} />
      </Popover>
    }
    onChange={e => onChange({ name: data.name, value: e.target.value, error: valid })}
    name={data.name}
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    value={data.value}
    style={{ width: '100%' }}
  />
}

const renderSelect = (data, onChange) => {
  const valid = validate(data)
  return <Select
    className={valid === undefined ? '' : "has-error"}
    name={data.name}
    disabled={data.disabled}
    showSearch
    mode={data.mode}
    style={{ width: '100%' }}
    placeholder={data.placeholder}
    optionFilterProp="children"
    onChange={value => onChange({ name: data.name, value: value, error: valid })}
    filterOption={(input, option) =>
      option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
    }
    value={data.value}>
    {data.options.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
  </Select>
}

const renderTextArea = (data, onChange) => {
  const valid = validate(data)
  return <TextArea
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    rows="3"
    name={data.name}
    onChange={e => onChange({ name: data.name, value: e.target.value, error: valid })}
    value={data.value}
    style={{ width: '100%' }}
  />
}

const renderRadioGroup = (data, onChange) => {
  const valid = validate(data)
  return <Radio.Group
    className={valid === undefined ? '' : "has-error"}
    style={{ width: '100%' }}
    name={data.name}
    onChange={e => onChange({ name: data.name, value: e.target.value, error: valid })}
    value={data.value}>
    {data.options.map((op, i) => (<Radio key={i} value={op.value}>{op.text}</Radio>))}
  </Radio.Group>
}

const renderRangePicker = (data, onChange) => {
  const valid = validate(data)
  return <RangePicker
    className={valid === undefined ? '' : "has-error"}
    name={data.name}
    onChange={(value, dateString) => onChange({ name: data.name, value: dateString, error: valid })}
    locale={data.language === 'th' ? localeTH : localeEN}
    value={data.value ? [
      moment(data.value[0]), moment(data.value[1])
    ] : null}
    style={{ width: '100%' }}
  />
}

const renderCheckAll = (data, onChange) => {
  const valid = validate(data)
  return <div>
    <Checkbox
      name={data.name + 'all'}
      indeterminate={data.value.length > 0 && data.value.length < data.options.length}
      onChange={e => (
        onChange({ name: data.name, value: e.target.checked ? data.options.map(x => x.value) : [], error: valid })
      )}
      checked={data.value.length === data.options.length}
      className="d-inline-block mr-2 border-right">{data.label}</Checkbox>
    <Checkbox.Group
      className={valid === undefined ? '' : "has-error"}
      name={data.name}
      options={data.options}
      onChange={checkedList => (
        onChange({ name: data.name, value: checkedList, error: valid })
      )}
      value={data.value || []} />
  </div>
}

const renderCustomSelect = (data, onChange) => {
  const valid = validate(data)
  return <CustomDropdown
    btStyle={data.btStyle}
    className={valid === undefined ? '' : "has-error"}
    selected={data.value}
    onSelect={item => onChange({ name: data.name, value: item.id, error: valid })}
    placeholder={data.placeholder}
    options={data.options} />
}

const renderTimePicker = (data, onChange) => {
  const valid = validate(data)
  return <TimePicker
    className={valid === undefined ? '' : "has-error"}
    style={{ width: '100%' }}
    placeholder={''}
    value={data.value ? moment(data.value, data.format || 'HH:mm') : null}
    onChange={(time, timeString) => onChange({ name: data.name, value: timeString, error: valid })}
    format={data.format || 'HH:mm'} />
}

const renderCheckbox = (data, onChange) => {
  const valid = validate(data)
  let className = ''
  if (data.label) className = 'd-inline-block mr-2'
  if (valid !== undefined) className += ' has-error'
  return <Checkbox
    style={{ width: '100%' }}
    onChange={e => onChange({ name: data.name, value: e.target.checked, error: valid })}
    checked={data.value}
    className={className}>{data.label || ''}</Checkbox>
}

export default (template, onChange) => {
  if (template.type === 'input') return renderInput(template, onChange)
  if (template.type === 'datepicker') return renderDatepicker(template, onChange)
  if (template.type === 'select') return renderSelect(template, onChange)
  if (template.type === 'textarea') return renderTextArea(template, onChange)
  if (template.type === 'radiogroup') return renderRadioGroup(template, onChange)
  if (template.type === 'rangepicker') return renderRangePicker(template, onChange)
  if (template.type === 'checkall') return renderCheckAll(template, onChange)
  if (template.type === 'customselect') return renderCustomSelect(template, onChange)
  if (template.type === 'timepicker') return renderTimePicker(template, onChange)
  if (template.type === 'checkbox') return renderCheckbox(template, onChange)
  return template.component
}
