import React, { Component } from 'react'
import { Table } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { exportExcel } from '../../utils/excel'

class StatTable extends Component {
  state = {
    selectedRows: []
  }

  exportExcel = item => {
    const table = `<table border='1px'>
      <tr>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Age</th>
      </tr>
      <tr>
        <td>Jill</td>
        <td>Smith</td>
        <td>50</td>
      </tr>
      <tr>
        <td>Eve</td>
        <td>Jackson</td>
        <td>94</td>
      </tr>
    </table>`
    console.log(item)
    exportExcel('example', table)
  }

  render() {
    const { selectedRows } = this.state
    const dataSource = [{
      key: '1',
      seq: 221,
      name: 'Air Transport Traffic at Domestic Airport'
    }, {
      key: '2',
      seq: 51,
      name: 'Monthly Scheduled Traffic for Year Operated by international Airport'
    }, {
      key: '3',
      seq: 224,
      name: 'Traffic of Commercial and Non Commercial Flight Classified By Airport / Month'
    }, {
      key: '4',
      seq: 198,
      name: 'Commercial Air Transport Statistics Classified By Airline Scheduled Flight (All Card)'
    }, {
      key: '5',
      seq: 19,
      name: 'Commercial Air Transport Statistics Classified By City Scheduled Flight (All Card)'
    }];

    const columns = [{
      title: 'Seq',
      dataIndex: 'seq',
    }, {
      title: 'Report',
      dataIndex: 'name',
      className: 'text-left',
    }, {
      title: '',
      dataIndex: '',
      key: 'x',
      width: 50,
      render: item => <button onClick={() => this.exportExcel(item)} className="btn btn-outline-success btn-sm">
        <FontAwesomeIcon icon="download" />
      </button>
    }]

    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ selectedRows })
      }
    }
    return (
      <div className="card card-body card-header-table form-inling text-primary">
        <div className="mb-2">
          <FontAwesomeIcon icon="plane" /><span className="ml-2">Operations report</span>
        </div>

        <Table
          style={{ minWidth: 800 }}
          rowSelection={rowSelection}
          className="tbody-center bg-white"
          dataSource={dataSource}
          pagination={false}
          footer={() => {
            if (selectedRows.length === 0) return false
            return <button className="btn btn-outline-success btn-sm">
              <FontAwesomeIcon icon="download" />
              <span className="ml-2">Export</span>
            </button>
          }}
          columns={columns} />
      </div>
    )
  }
}

export default StatTable